export default {
  text:
    'Suppose that the Austrian government has heard about problematic forms of feeding ("fattening") geese in Member State Germany. Such geese are forcefully fed with excessive amounts of feedstuffs, which leads to the multiplication of their weight until the point of time of their slaughter. Suppose furthermore that such forms of feeding are prohibited in Austria and that no relevant EU secondary law exists which regulates such feeding methods. The Austrian government considers, against this background to implement an import ban for any geese from Germany, since it cannot be ruled out with certainty that the consumption of such fattened geese is dangerous to human health, even though such risk is highly unlikely. Will such a regulatory project comply with the fundamental freedoms of EU law?'
};



